import React from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, TipoProduto } from '../../../../models/dtos/mlf/tipoProduto';
import { TIPO_ITEM_ENUM_VALUES } from '../../../../models/enumerated/ger/tipoItemEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface TipoProdutoEditViewProps extends EditViewProps<TipoProduto> {}

const TipoProdutoEditView: React.FC<TipoProdutoEditViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const selectedItem = getSelectedItem(props);
  const grupoTipoProdutoDTO = globalReducer.globalParameter.grupoEmpresaConfig.grupoProduto.grupoTipoProduto;
  const formProps = useForm<TipoProduto>({
    defaultValues: { ...selectedItem, grupoTipoProduto: { id: grupoTipoProdutoDTO.id } },
    mode: 'onBlur',
  });

  return (
    <WindowComponent title="Manutenção de Tipo de Produto">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline hidden={true} formProps={formProps} id="grupoTipoProduto.id" />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="codigo"
              inputProps={true}
              label="Código"
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 10) } }}
              isPrependLabel={true}
              placeholder="Informe o código"
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
            />
          </Col>
          <Col>
            <InputGroupInline
              id="descricao"
              inputProps={true}
              label="Descrição"
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              isPrependLabel={true}
              placeholder="Informe a descrição"
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="tipoItemStr"
              label="Tipo"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              inputDropDownProps={{ optionsProvider: TIPO_ITEM_ENUM_VALUES }}
              placeholder="Informe o Tipo de item"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default TipoProdutoEditView;
